<template>
    <div class="baldetail">
    	<fixedheader :title="title"></fixedheader>
      <div class="ba-top htwo">
        <div :class="[stateAC == index ? 'ba-top-ac' : '']"
             v-for="(item, index) in state" :key="index"
              @click="checkState(index)"
        >{{item.name}}</div>
      </div>

      <div>
        <div>
          <div v-show="stateAC == 0">
            <mt-field label="姓名" placeholder="请输入姓名" v-model="zhifu.username"></mt-field>
            <mt-field label="电话号码" placeholder="请输入电话号码" v-model="zhifu.tel"></mt-field>
            <mt-field label="支付宝号" placeholder="请输入支付宝号" v-model="zhifu.hao"></mt-field>
            <mt-field label="提现金额" v-model="zhifu.mon" :placeholder="'当前可提现'+userData.use_money+'元'"></mt-field>
          </div>

          <div v-show="stateAC == 1">
            <mt-field label="姓名" placeholder="请输入姓名" v-model="yinhang.username"></mt-field>
            <mt-field label="电话号码" placeholder="请输入电话号码" v-model="yinhang.tel"></mt-field>
            <mt-field label="银行卡号" placeholder="请输入银行卡号" v-model="yinhang.hao"></mt-field>
            <mt-field label="开户行" placeholder="请输入开户行" v-model="yinhang.hang"></mt-field>
            <mt-field label="提现金额" v-model="yinhang.mon" :placeholder="'当前可提现'+userData.use_money+'元'"></mt-field>
          </div>
          <!--<div class="sub-div">最多可提现 {{moneySize.over_money}} 元, 最少为 {{moneySize.begin_money}} 元</div>-->
          <div class="site-btn-box site-btn-box1">
            <div @click="showPassfun" class="site-btn back-to baldetail-btn">提现</div>
          </div>
        </div>
      </div>

    </div>
</template>

<script>
import fixedheader from "../common/FixedHeader"
//import navRight from './module/navigation.vue'
    export default {
        data () {
            return {
            	title:'提现',
              state:[
                {name:'转账到支付宝',inname:'姓名',tel:'电话号码',cardnum:'支付宝号',mon:'提现金额'},
                {name:'转账到银行卡',inname:'姓名',tel:'电话号码',cardnum:'银行卡',mon:'提现金额'}
              ],
              stateAC:0,
              zhifu:{
                username:'',
                tel:'',
                hao:'',
                mon:''
              },
              yinhang:{
                username:'',
                tel:'',
                hao:'',
                hang:'',
                mon:''
              },
              moneySize:{},
              userData:{
              	use_money:50.0
              },
              captcha:'',  //验证码
              dao:60,   //倒计时多少秒
              daoshow:true,   //倒计时show
              showPass:false, //二级密码开关
              balpassword:'', //二级密码

              set_password:'',  //是否有二级密码 0:没有
              showSet:false,  //设置和忘记二级密码页面开关
              setTitle:'提示',  //设置密码弹框的标题
              setpassword:'', //这是二级密码
              setpasswordpp:'',  //确认二级密码
              passtell:'',  //设置密码手机号
            }
        },
        methods: {
          checkState:function (index) {
            this.stateAC = index
          },

          showPassfun:function () {   //验证输入信息是否完整
            if(this.set_password == 0){
              this.noSet_pass()
              return
            }
            if(this.stateAC == 0){
              if(this.zhifu.username.length == 0){
                this.$MessageBox('提示', '请输入姓名');
                return
              }
              if(this.zhifu.tel.length == 0){
                this.$MessageBox('提示', '请输入电话');
                if(this.$Num.tellNum(this.zhifu.tel)){
                }else{return}
              }else if(this.zhifu.mon.length == 0){
                console.log(this.zhifu.mon)
                this.$MessageBox('提示', '请输入金额');
                return
              }
              if(this.zhifu.hao.length == 0){
                this.$MessageBox('提示', '请输入支付宝号');
                return
              }
            }else{
              if(this.yinhang.username.length == 0){
                this.$MessageBox('提示', '请输入姓名');
                return
              }
              if(this.yinhang.tel.length == 0){
                this.$MessageBox('提示', '请输入电话');
                if(this.$Num.tellNum(this.yinhang.tel)){
                }else{return}
              }else if(this.yinhang.mon.length == 0){
                this.$MessageBox('提示', '请输入金额');
                return
              }
              if(this.yinhang.hao.length == 0){
                this.$MessageBox('提示', '请输入银行卡号');
                return
              }
              if(this.yinhang.hang.length == 0){
                this.$MessageBox('提示', '请输入开户行');
                return
              }
            }
            this.showPass = true
          },


          tixian:function () {
            if(this.balpassword.length == 0){return}

            let obj = {}
            let data = {}
            obj.set_password = this.balpassword
            if(this.stateAC == 0){  //支付宝
              data = this.zhifu
              obj.type = 1
              obj.money = data.mon
              obj.username = data.username
              obj.account = data.hao
              obj.tell = data.tel
              if(!this.$Num.tellNum(obj.tell)){return}
            }else{  //银行
              data = this.yinhang
              obj.type = 2
              obj.money = data.mon
              obj.username = data.username
              obj.account = data.hao
              obj.tell = data.tel
              obj.card_type = data.hang
              if(!this.$Num.tellNum(obj.tell)){return}
              if(!this.$Num.checkBankNo(obj.account)){
                this.$MessageBox('提示','请输入正确银行卡号')
                return
              }
            }
            if(obj.username.length == 0){
              this.$MessageBox('提示', '请输入姓名');
              return
            }
            if(obj.account.length == 0){
              this.$MessageBox('提示', '请输入账号');
              return
            }else if(obj.money.length == 0 || Number(obj.money) <= 0){
              this.$MessageBox('提示', '请输入金额');
              return
            }
            this.$Indicator.open('加载中...');

            this.$http.post('/User/tixian',obj,res=>{
              this.$router.go(-1)
              this.$Toast('提现成功')
            },err=>{},res=>{this.$Indicator.close()})
          },

          aa:function () {

          },

        },
        mounted(){
        	
        },
        components:{fixedheader}
    }
</script>

<style scope>
.baldetail{
  height:100%;
  overflow-y: auto;
}
  .ba-top{
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #fff;
    margin-bottom: .3rem;
    border-top: 1px #e6e6e6 solid;
  }
  .ba-top>div{
    height:1rem;
    line-height: 1rem;

    box-sizing: border-box;
  }
  .ba-top-ac{
    color: #FF5773;
    border-bottom: 3px solid #FF5773;
  }
  .baldetail .site-btn-box{
    margin-top:1.5rem;
  }
  .baldetail .site-btn-box2{
    margin-top: .35rem;
    width:80%;
  }
  .baldetail .login-go-input{
    width:85%;
    padding-right:1.5rem;
    box-sizing: border-box;
  }
  .baldetail .login-go-input-password{
    width:100%;
  }
.baldetail .site-btn-box2>.site-btn{
  color: #fff !important;
  border:none !important;
}
  .site-btn-box2>.baldetail-btn{
    height: .7rem;
    line-height: .7rem;
    font-size: .33rem;
  }
  .baldetail .baldetail-btn{
    border-radius: 1rem;
    padding:0 0.3rem;
    box-sizing: border-box;
  }
  .baldetail .slider-slider{
    margin-top: 0.2rem;
  }
  .wang-pass{
    margin-right:.2rem;
    border-bottom:1px solid #19abfa;
  }
  .baldetail .slider-box-pass{
    padding:.3rem;
    height: auto;
  }

.have-state{
  position: absolute;
  right:5%;
  top:21%;
  font-size: .3rem;
  color: #ccc;
}
.baldetail .have-state1{
  position: absolute;
  right:12%;
  top:22%;
  font-size: .3rem;
}
  .slider-bottom-btn{
    display: flex;
    width:100%;
    justify-content: space-between;
    align-items: flex-end;
  }
  .slider-bottom-btn .site-btn-box2{
    width:50%;
  }
  .slider-bottom-btn>span{
    height:.35rem;
    font-size: .26rem;
    margin-left:.3rem;
  }
</style>



